@import url(https://fonts.googleapis.com/css?family=Montserrat:200,300,400,700,900&display=swap);
:root {
    --gradient: linear-gradient(135deg, #72edf2 10%, #6666a8 100%);
    --bg-color: #283e70;
    --bg-color--card: #338881;

    --main-text-color: #ffffff;

    --main-border-radius: 25px;
    --secondary-border-radius: 15px;
}

body {
    width: 100%;
    height: 100vh;
    font-family: 'Montserrat', sans-serif;
    background-color: #283e70;
    background-color: var(--bg-color);
    color: #ffffff;
    color: var(--main-text-color);
}

